import * as React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { useRequest } from 'ahooks';


const MuiButton = (props) => {
    let {
        id,
        key,
        children,
        classes,
        color,
        component,
        disabled,
        disableElevation,
        disableFocusRipple,
        disableRipple,
        endIcon,
        fullWidth,
        href,
        size,
        startIcon,
        // sx,
        variant,
        nClicks,
        debounceWait,
        setProps
    } = props;

    const { run: onClick} = useRequest(
        () => setProps({nClicks: nClicks + 1}),
        {
            debounceWait: debounceWait,
            debounceLeading: true,
            manual: true
        }
    )


    const renderElement = (
        <Button
            id={id}
            key={key}
            classes={classes}
            color={color}
            component={component}
            disabled={disabled}
            disableElevation={disableElevation}
            disableFocusRipple={disableFocusRipple}
            disableRipple={disableRipple}
            endIcon={endIcon}
            fullWidth={fullWidth}
            href={href}
            size={size}
            startIcon={startIcon}
            // sx={sx}
            variant={variant}
            onClick={onClick}
        >
            {children}
        </Button>
    );

  return renderElement;
}

MuiButton.propTypes = {
    /**
     * id
     */
    id: PropTypes.string,

    /**
     * key
     */
    key: PropTypes.string,

    /**
     * 
      */
    children: PropTypes.node,

    /**
     * 
     */
    classes: PropTypes.object, 

    /**
     * 
     */
    color: PropTypes.oneOf(['inherit', 'primary', 'secondary', 'success', 'error', 'info', 'warning', 'string']),

    /**
     * 
     */
    component: PropTypes.elementType,
    
    /**
         * 
         */
    disabled: PropTypes.bool,

    /**
     * 
     */
    disableElevation: PropTypes.bool,

    /**
     * 
     */
    disableFocusRipple: PropTypes.bool,

    /**
     * 
     */
    disableRipple: PropTypes.bool,

    /**
     * 
     */
    endIcon: PropTypes.node,

    /**
     * 
     */
    fullWidth: PropTypes.bool,

    /**
     * 
     */
    href: PropTypes.string,

    /**
     * 
     */
    size: PropTypes.oneOf(['small', 'medium', 'large', 'string']),

    /**
     * 
     */
    startIcon: PropTypes.node,

    /**
     * CSS
     */
    // sx: PropTypes.oneOf(
    //     [
    //         PropTypes.array([PropTypes.func, PropTypes.object, PropTypes.bool]),
    //         PropTypes.func,
    //         PropTypes.object,
    //     ]
    // ),

    /**
     * 
      */
    variant: PropTypes.oneOf(['outlined', 'contained', 'text', 'string']),

    /**
     * 
     * `0`
     */
    nClicks: PropTypes.number,

    /**
     * 
     */
    debounceWait: PropTypes.number,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

MuiButton.defaultProps = {
    color: 'primary',
    disabled: false,
    disableElevation: false,
    disableFocusRipple: false,
    disableRipple: false,
    fullWidth: false,
    size: 'medium',
    variant: 'text',
    nClicks: 0,
    debounceWait: 200
};

export default MuiButton;